<template>
  <div class="mvplay">
    <video  :src="url" controls v-if="url" class="mvplay__item">
    </video>
  </div>
</template>

<script>
import { getMVUrl } from '../api/api_video'
export default {
  name: 'mvplay',
  data () {
    return {
      id: this.$route.params.id,
      url: null
    }
  },
  methods: {
    async getData (id) {
      const res = await getMVUrl(id)
      this.url = res.data.data.url
    }
  },
  created () {
    this.getData(this.id)
  }
}
</script>

<style lang="scss" scoped>
.mvplay{
  width: 100%;
  height: 100%;
  &__item{
    margin: 0 5%;
    width: 90%;
    aspect-ratio: 4/3;
    max-height: 90%;
  }
}
</style>
